import React, { Component } from 'react'
import axios from 'axios'
import { Card, CardBody, Button, Row, Col } from 'reactstrap'
axios.defaults.baseURL = 'https://lastarzi2.herokuapp.com'
//axios.defaults.baseURL = "http://localhost:5000";
//axios.defaults.timeout = 10000;

export default class MailConfirm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      success: false,
      err: false
    }
    this._isMounted = false
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  componentDidMount() {
    const userkey = this.props.match.params.userkey
    console.log('userkey', userkey)
    if (userkey !== '') {
      axios
        .post(
          '/backendapi/users/verify',
          { key: userkey },
          {
            headers: {
              Authorization: 'Bearer ' + localStorage.getItem('authtoken')
            }
          }
        )
        .then((res) => {
          this.setState({
            success: true,
            err: false
          })
        })
        .catch((e) => {
          this.setState({
            success: false,
            err: true
          })
        })
    } else {
    }
  }

  handleSubmit(e) {}

  render() {
    return (
      <Row className="m-0">
        <Col sm="12">
          <Card className="auth-card bg-transparent shadow-none rounded-0 mb-0 w-100">
            <CardBody className="text-center">
              <h1
                className="font-large-2 my-1"
                style={{
                  color: this.state.success
                    ? 'lightgreen'
                    : this.state.err
                    ? 'red'
                    : 'white'
                }}
              >
                {this.state.success
                  ? 'Your email is successfully verified ! You can login now'
                  : this.state.err
                  ? 'Link Expired !!!!!!!!!!!!!!!'
                  : 'Please wait while we confirm your email ...'}
              </h1>

              <Button.Ripple
                tag="p"
                onClick={() => {
                  this.props.history.push('/login')
                }}
                color="primary"
                size="lg"
                className="mt-2"
                disabled={!this.state.success || this.state.err}
              >
                {this.state.success
                  ? 'Go to Login'
                  : this.state.err
                  ? 'Link Expired !'
                  : 'Checking ...'}
              </Button.Ripple>
            </CardBody>
          </Card>
        </Col>
      </Row>
    )
  }
}
